.socials__desc-update {
  display: inline-block;
  vertical-align: top;
  size: width('socials__desc-update.png', 2) height('socials__desc-update.png', 4);
  background: inline('socials__desc-update.png') no-repeat;
  background-size: size('socials__desc-update.png', 2);
  cursor: pointer;
  transition: transform 0.2s ease;
  outline: none;

  &:hover {
    background-position: 0 calc(height('socials__desc-update.png', 4) * -1);
  }
}
