.principles__support {
  display: inline-block;
  font-family: var(--sans-serif-family);
  font-size: 15px;
  line-height: 24px;
  background-color: #fe5c0c;
  color: #fff;
  padding: 4px 18px;
  border-radius: 4px;
  box-shadow: 0 1px 0 #9a3807;
  box-sizing: border-box;
  cursor: pointer;
  position: absolute;
  top: 77px;
  right: 15px;
  margin-right: 5px;
  cursor: pointer;
  z-index: 5;
  transform: translateZ(1px);

  &:hover {
    background-color: #ee4e00;
    color: #fff;
  }
}
