.knowleges__tile--more {
  position: absolute;
  right: 0;
  bottom: -11px;
  cursor: pointer;
  display: none;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 35px;
    left: 0;
    bottom: 0;
    border: 1px solid #D6CEE5;
    transition: border-color 0.4s ease-out;
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 30px;
    left: 0;
    bottom: -5px;
    border: 1px solid #D6CEE5;
    margin: 5px 0 0 5px;
    background-color: #F1F0EC;
    transition: border-color 0.4s ease-out;
  }

  &:hover:before,
  &:hover:after {
    transition: border-color 0s linear;
    border-color: color(#ff5400 l(85%));
  }

  .knowleges__tiles--scoped & {
    display: block;
  }
}
