.likely,.likely__widget {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  margin: 0;
  text-indent: 0!important;
  list-style: none!important;
  font-weight: 400;
  font-family: var(--sans-serif-family);
  font-size: 18px;
  transform: translateZ(2px);
  padding-top: 3px;
}

.likely {
  opacity: 0;
  font-size: 0!important;
}

.likely .likely__counter{
  display: none !important;
}

.likely_visible {
  opacity: 1;
  transition: opacity .1s ease-in;
}

.likely>* {
  display: inline-block;
  visibility: hidden;
}

.likely_visible>* {
  visibility: inherit;
}

.likely__widget {
  display: inline-block;
  padding: 3px 4px;
  position: relative;
  white-space: nowrap;
}

.likely__button,.likely__counter {
  text-decoration: none;
  text-rendering: optimizeLegibility;
  display: inline-block;
  margin: 0;
  outline: 0;
}

.likely__button {
  position: relative;
  cursor: pointer;
  user-select: none;
}

.likely__counter {
  display: none;
  position: relative;
  font-weight: 400;
}

.likely_ready .likely__counter {
  display: inline-block;
}

.likely_ready .likely__counter_empty {
  display: none;
}

.likely__widget,.likely__icon,.likely__button,.likely__counter {
  vertical-align: top;
}

.likely__widget {
  transition: background .33s ease-out,color .33s ease-out,fill .33s ease-out;
  line-height: inherit;
}

.likely__widget:hover,.likely__widget:active,.likely__widget:focus {
  transition: none;
  cursor: pointer;
}

.likely__icon {
  position: relative;
  text-align: left;
  display: inline-block;
}

.likely svg {
  position: absolute;
}

.likely__button,.likely__counter {
  line-height: inherit;
  cursor: inherit;
}

.likely__button:empty {
  display: none;
}

.likely__counter {
  text-align: center;
}

.likely .likely__widget {
  color: #000;
  background: #ececec;
  background: rgba(231,231,231,.8);
}

.likely .likely__counter {
  background-size: 1px 1px;
  background-repeat: repeat-y;
  background-image: linear-gradient(to right,rgba(0,0,0,.2)0,rgba(0,0,0,0).5px,rgba(0,0,0,0)100%);
}

.likely-light .likely__widget {
  color: #fff;
  fill: #fff;
  background: rgba(236,236,236,.16);
  text-shadow: rgba(0,0,0,.2)0 0 .33em;
}

.likely-light .likely__counter {
  background-image: linear-gradient(to right,rgba(255,255,255,.4)0,rgba(255,255,255,0).5px,rgba(255,255,255,0)100%);
}

.likely__widget_facebook {
  fill: #425497;
}

.likely__widget_facebook:hover,.likely__widget_facebook:active,.likely__widget_facebook:focus {
  background: rgba(207,212,229,.8);
}

.likely-light .likely__widget_facebook:hover,.likely-light .likely__widget_facebook:active,.likely-light .likely__widget_facebook:focus {
