.principle1__aside {
  font-family: var(--sans-serif-family);
  font-size: 15px 22px;
  width: 235px;
  position: absolute;
  bottom: 103px;
  right: 40px;
  box-sizing: border-box;
  padding-left: 62px;
  min-height: 66px;
  text-align: left;

  p {
    font-size: 15px 22px;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 63px;
    background: inline('principle1__aside.png') no-repeat;
    background-size: size('principle1__aside.png', 2);
  }
}
