.poem {
  text-align: center;
  font-family: var(--sans-serif-family);
  font-size: 16px 22px;
  margin-top: 59px;
  padding-bottom: 86px;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    size: 100% 84px;
    background: url('poem__bg.png') center no-repeat;
    background-size: size('poem__bg.png', 2);
  }
}
