.request__placeholder2 {
  text-transform: uppercase;
  font-size: 10px 13px;
  font-family: var(--sans-serif-family);
  letter-spacing: 1px;
  position: absolute;
  bottom: -20px;
  left: 0;
  opacity: 0;
  transition: opacity 0.1s ease-out;

  input:focus + * + &,
  textarea:focus + * + & {
    opacity: 1;
  }

  .request__inp--not-empty + * + & {
    opacity: 1;
  }

  .request__dl--error & {
    color: #ee4e00;
    opacity: 1;
  }
}
