.principle3__doc {
  font-family: var(--sans-serif-family);
  position: absolute;
  padding-left: 60px;
  width: 260px;
  bottom: 129px;
  min-height: 70px;

  &:before {
    content: '';
    position: absolute;
    background: inline('principle3__doc.png') no-repeat;
    width: 48px;
    height: 63px;
    left: 0;
    top: 0;
    background-size: size('principle3__doc.png', 2);
  }

  a {
    color: #ff5400;
    border-bottom: 1px solid #ff5400;
    border-bottom: 1px solid rgba(255, 84, 0, 0.3);

    &:hover {
      color: #5f15cd;
      border-bottom: 1px solid #5f15cd;
      border-bottom: 1px solid rgba(95, 21, 205, 0.3);
    }
  }
}
