.rates__duration {
  color: #ff5400;
  font-family: var(--mono-bold-family);
  font-weight: bold;
  font-size: 18px 18px;
  margin-top: -6px;
  position: absolute;
  letter-spacing: 0;
  top: 69px;
  left: 40.5vw;
  transform: rotate(270deg);
  text-align: right;
  width: 69px;
}

@media (width >= 1433px) {
  .rates__duration {
    left: 580px;
  }
}

@media (width <= 1300px) {
  .rates__duration {
    left: 39.9vw;
    top: 74px;
  }
}

@media (width <= 1230px) {
  .rates__duration {
    left: 39.8vw;
    top: 78px;
  }
}

@media (width <= 1160px) {
  .rates__duration {
    left: 39.5vw;
    top: 80px;
    font-size: 16px;
  }
}

@media (width <= 1030px) {
  .rates__duration {
    left: 39vw;
    top: 85px;
  }
}

@media (width <= 900px) {
  .rates__duration {
    left: 38.2vw;
    top: 91px;
    font-size: 14px;
  }
}

@media (width <= 840px) {
  .rates__duration {
    left: 37.4vw;
    top: 92px;
    font-size: 13px;
  }
}
