.top-menu {
  font-family: var(--sans-serif-family);
  display: inline-block;
  vertical-align: top;
  width: 340px;

  a {
    color: #5f15cd;
    border-bottom: 1px solid #5f15cd;
    border-bottom: 1px solid rgba(95, 21, 205, 0.3);

    &:hover {
      color: #ff5400;
      border-color: #ff5400;
      border-color: rgba(255, 84, 0, 0.4);
    }
  }
}
