.free-docs__item{
  box-sizing: border-box;
  display: inline-block;
  font-family: "Museo Sans","Helvetica",sans-serif;
  font-size: 15px;
  line-height: 1.2;
  padding-right: 3.3%;
  min-width: 185px;
  vertical-align: top;
  position: relative;
}


.free-docs__item--wide{
  font-family: "PT Serif",serif;
  box-sizing: border-box;
  padding-right: 50px;
  width: 68%;
  margin-bottom: 60px;
}

.free-docs__item--short{
  margin-top: 3px;
  font-size: 15px;
  line-height: 22px;
  box-sizing: border-box;
  padding-right: 25px;
  width: 32%;
}