.articles__name {
  z-index: 4;
  padding: 4.3% 0 0;
  border-bottom: 1px solid #5f15cd;
  border-bottom: 1px solid rgba(95,21,205,0.3);

  a.articles__item:hover &,
  a&:hover {
    border-color: #ff5400;
    border-color: rgba(255,84,0,0.4);
  }

  .articles__item--short & {
    font-size: 15px 30px;
  }

  .articles__item--middle & {
    font-size: 24px 36px;
  }

  .articles__item--wide & {
    font-size: 24px 36px;
  }

  .articles__item--stared &:before {
    content: '';
    position: absolute;
    right: -43px;
    top: -43px;
    size: size('articles__item--stared.png', 2);
    background: url('articles__item--stared.png');
    background-size: size('articles__item--stared.png', 2);
  }
}
