.principle8__tab {
  display: inline-block;
  vertical-align: top;
  margin: 0 13px;
  font-family: var(--sans-serif-family);
  cursor: pointer;
  color: #5f15cd;
  border-bottom: 1px dashed #5f15cd;
  border-bottom: 1px dashed rgba(95, 21, 205, 0.3);
  line-height: 14px;
  position: relative;
  z-index: 6;

  &:hover {
    color: #ff5400;
    border-color: #ff5400;
    border-color: rgba(255, 84, 0, 0.4);
  }
}
