.request__placeholder1 {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 17px 27px;
  color: #858585;
  padding: 3px 0;
  z-index: 1;
  transition: opacity 0.1s ease-out;
  opacity: 1;

  input:focus + &,
  textarea:focus + & {
    opacity: 0;
  }

  .request__dl--error & {
    color: #ee4e00;
    opacity: 0;
  }

  .request__inp--not-empty + & {
    opacity: 0;
  }
}
