.principle1 {
  a {
    color: #ff5400;
    border-bottom: 1px solid #ff5400;
    border-bottom: 1px solid rgba(255, 84, 0, 0.3);

    &:hover {
      color: #5f15cd;
      border-bottom: 1px solid #5f15cd;
      border-bottom: 1px solid rgba(95, 21, 205, 0.3);
    }
  }

  .g-quote-link {
    border: none;

    span {
      color: #ff5400;
      border-bottom: 1px solid #ff5400;
      border-bottom: 1px solid rgba(255, 84, 0, 0.3);
    }

    &:hover  {
      border: none;

      span {
        color: #5f15cd;
        border-bottom: 1px solid #5f15cd;
        border-bottom: 1px solid rgba(95, 21, 205, 0.3);
      }
    }
  }

  .principle1__head + p {
    margin-top: 20px;
  }
}
