.menu {
  margin: 65px 0 0;
  position: relative;
  z-index: 2;
  padding: 0 27px 0 0;

  a {
    color: #000000;
  }

  &:after {
    content: '';
    display: inline-block;
    background: url('menu__bullets.png') no-repeat;
    size: width('menu__bullets.png', 4) height('menu__bullets.png', 2);
    background-size: size('menu__bullets.png', 2);
    margin: 5px 0 0 15px;
  }
}
