.free-docs {
  margin: 50px 21px 0 0;
  outline: #37126C solid 1px;
  outline-offset: 1px;
  border: 2px solid #562996;
  padding: 7px 8px;
  position: relative;
  min-width: 865px;
p {
  max-width: 520px;
}

img {
  max-width: 100%;
  }
}

.free-docs__contain {
  background: #532b96;
  background: linear-gradient(#401D73, #5D2AA6);
  padding: 0 30px 30px;
  color: #f1f0ec;
  border: 1px solid #532b96;
  outline: #532b96 solid 1px;
  outline-offset: 1px;
  font-size: 0;
  position: relative;
}

.free-docs__stars1{
  position: absolute;
  top: 0;
  right: 0;
  width: 486px;
  height: 251px;
  background: url(stars.png) 247% 11%/86% 67% no-repeat;
}

.free-docs__stars2{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 546px;
  height: 300px;
  background: url(stars2.png) -18% 83%/64% 60% no-repeat;
}

.free-docs__list{
  margin-top: 40px;
}

