.content-cards__item--numbers{
  padding: 15px 23px 15px 0;
  color: #fc551e;
  text-align: center;

& .content-cards__contain{
    padding-right: 0;
  }

& .content-cards__name{
    font-family: 'PT Mono', sans-serif;
    font-weight: bold;
    display: block;
    font-size: 108px;
    margin-top: 20px;
    border: none;
  }

& .content-cards__text{
    display: inline-block;
    font-size: 16px;
    color: #fc551e;
    text-align: center;
    margin-top: 30px;
    max-width: 310px;
    border: none;
  }
}