.knowleges__name {
  position: absolute;
  top: 0;
  left: 0;
  right: 35px;
  bottom: 0;
  border: 1px solid #D6CEE5;
  padding: 15px 25px 15px 15px;
  z-index: 2;
  transition: border-color 0.4s ease-out, color 0.4s ease-out;
  color: #5f15cd;
  background: #fff;

  .knowleges__tile:hover & {
    color: #ff5400;
  }

  .knowleges__tile:hover & {
    border-color: color(#ff5400 l(85%));
    transition: border-color 0s linear, color 0s linear;
  }

  .knowleges__tile--whited & {
    background-color: #fff;
    border: 0;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 1px solid #D6CEE5;
      transition: border-color 0.4s ease-out;
    }
  }

  .knowleges__tile--whited:hover &:after {
    border-color: color(#ff5400 l(85%));
    transition: border-color 0s linear;
  }

  .knowleges__tile--more & {
    top: 10px;
    left: 10px;
    bottom: -10px;
    background-color: #fff;
    right: 25px;

  }
}
