.top-panel__documents.top-panel__documents{
  padding-bottom: 140px;
  background: url(group@3x.png) no-repeat 40% 105% / 80% 80%;
}

.l-documents-title{
  text-align: center;
  position: relative;
}

.documents-title__head{
  font-family: 'PT Mono', sans-serif;
  font-size: 40px;
  line-height: 45px;
  font-weight: bold;
  letter-spacing: -2px;
}

.document-title__desc{
  max-width: 575px;
  margin: 10px auto 0;
  font-size: 18px;
  line-height: 30px;
  display: block;
  text-align: center;
}

.document-title__fix{
  margin: 0;
}