.request__sbm {
  background-color: #fe6418;
  color: #fff;
  border: 0;
  box-shadow: 0 1px 0 #9a3d0f;
  border-radius: 4px;
  font-size: 22px 24px;
  padding: 10px 50px 12px;
  outline: none;
  cursor: pointer;
  transition: opacity 0.2s ease-out;

  &:hover {
    background-color: #ee4e00;
  }

  .request__dl--done & {
    opacity: 0;
  }
}
