.advices {
  font-family: var(--sans-serif-family);
  position: relative;
  margin-left: -10px;

  &:after {
    content: '';
    position: absolute;
    top: -42px;
    right: 30px;
    background: url('advices__stars.png');
    size: size('advices__stars.png', 2);
    background-size: size('advices__stars.png', 2);
  }
}
