.knowleges__tile {
  font-size: var(--default-font-size);
  display: inline-block;
  vertical-align: top;
  width: 25%;
  font-family: var(--sans-serif-family);
  height: 198px;
  box-sizing: border-box;
  position: relative;
  margin-bottom: 25px;

  .knowleges__tiles--scoped &:nth-child(8) {
    visibility: hidden;
  }
}
