.content-cards__list{
  margin-top: 45px;
  font-size: 0;
}

.content-cards-list__title{
  margin-bottom: 5px;
  font-weight: bolder;
  font-family: "PT Mono", sans-serif;
  font-size: 20px;
  letter-spacing: -0.4px;
}

.content-cards-list__descr{
  margin-bottom: 30px;
  font-family: "PT Serif", sans-serif;
  font-size: 18px;
}

.content-cards__contain {
  display: block;
  position: relative;
  z-index: 4;
  height: 100%;

.content-cards__item--short & {
  padding: 10px;
}

.content-cards__item--wide & {
  padding: 10px 50px 10px 10px;
  }

.content-cards__item--middle & {
  padding: 10px 50px 10px 10px;
  }
}

@media screen and (max-width: 1120px) {
    .content-cards__contain {
      .content-cards__item--short & {
      padding: 0;
      padding-bottom: 25px;
    }

      .content-cards__item--wide & {
      padding: 10px;
    }

      .content-cards__item--middle & {
      padding: 10px;
    }
  }

  .content-cards__item {
    padding: 10px;
  }
}

.content-cards__name {
  font-family: "PT Serif", sans-serif;
  z-index: 4;
  font-size: 24px 32px;
  border-bottom: 1px solid rgba(93,42,166, 0.2);

.content-cards__item--stared &:before {
   content: '';
   position: absolute;
   right: -43px;
   top: -43px;
   background: url('content-cards__item--stared.png');
  }
}


.content-cards__item--white:hover .content-cards__name{
  border-bottom: 1px solid rgba(255,84,0,0.4);
}

.content-cards__date{
  font-family: "Museo Sans","Helvetica",sans-serif;
  font-size: 15px;
  bottom: 26px;
  position: absolute;
  right: 10px;
  color: #000000;
}

.content-cards__text {
  color: #000;
  font-family: var(--sans-serif-family);
  font-size: 15px;
  display: block;
  text-align: left;

  .content-cards__item--short & {
  margin-top: 5px;
  }

  .content-cards__item--middle & {
    margin-top: 5px ;
  }

  .content-cards__item--wide & {
    margin-top: 5px;
  }
}

.content-cards__item {
  display: inline-block;
  vertical-align: top;
  /*font-size: var(--default-font-size);*/
  box-sizing: border-box;
  height: 186px;
  padding: 10px;
  position: relative;
  margin-bottom: 40px;
  padding-right: 50px;
  font-size: 0;
}

.content-cards__item--short {
  width: 20%;
  font-family: var(--sans-serif-family);
  padding: 15px 35px 15px 15px;
}

.content-cards__item--wide {
  width: 40%;
}

.content-cards__item--middle {
  width: 60%;
}