.steps__item {
  display: inline-block;
  vertical-align: top;
  font-family: var(--sans-serif-family);
  font-size: 15px 22px;
  position: relative;
  margin-right: 8px;

  &:after {
    content: '\2219';
    position: absolute;
    right: -12px;
    top: 0;
  }

  &:last-child:after {
    display: none;
    margin-right: 0;
  }
}
