.principle-title__achieve-element {
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
  size: 211px;
  background-image: linear-gradient(to bottom, #470dbd 0%, #7526d6 100%);
  position: relative;

  &:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -75px 0 0 -66px;
  }
}
