.request__read-more {
  font-size: 15px 22px;
  display: inline-block;
  vertical-align: top;
  width: 18.5%;
  margin-left: 12.53%;
  font-family: var(--sans-serif-family);

  a {
    padding: 6px 0 0;
    border-bottom: 1px solid #5f15cd;
    border-bottom: 1px solid rgba(95, 21, 205, 0.3);

    &:hover {
      border-bottom: 1px solid #ff5400;
      border-bottom: 1px solid rgba(255, 84, 0, 0.4);
    }
  }
}
