.request__text {
  border: none;
  border-bottom: 1px solid #cccccc;
  font-size: 17px 24px;
  padding: 6px 0;
  color: #000;
  outline: none;
  box-sizing: border-box;
  width: 100%;
  resize: none;
  height: 34px;
  min-height: 40px;
  position: relative;
  background-color: transparent;
  display: block;
  z-index: 2;

  .request__dl--error & {
    color: #ee4e00;

    &:focus {
      border-color: #ff5400;
    }
  }
}
