.principle8__tabs {
  text-align: center;
  position: absolute;
  top: 0;
  margin-top: -30px;
  left: -10px;
  right: -10px;
  z-index: 4;
  margin-left: -7px;
  transform: translateZ(1px);

  &:before {
    content: '';
    position: absolute;
    z-index: 4;
    top: -20px;
    height: 53px;
    background-color: rgba(255,255,255,0.95);
    left: 0;
    right: 0;
  }
}
