.grid__item {
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  width: var(--grid-item);
  padding-right: var(--gutter);
  font-size: var(--default-font-size);

  &--2 {
    width: calc(var(--grid-item) * 2);
  }

  &--3 {
    width: calc(var(--grid-item) * 3);
  }

  &--4 {
    width: calc(var(--grid-item) * 4);
  }

  &--5 {
    width: calc(var(--grid-item) * 5);
  }

  &--6 {
    width: calc(var(--grid-item) * 6);
  }

  &--7 {
    width: calc(var(--grid-item) * 7);
  }

  &--8 {
    width: calc(var(--grid-item) * 8);
  }

  &--9 {
    width: calc(var(--grid-item) * 9);
  }

  &--10 {
    width: calc(var(--grid-item) * 10);
  }

  &--11 {
    width: calc(var(--grid-item) * 11);
  }

  &--12 {
    width: calc(var(--grid-item) * 12);
  }

  &--13 {
    width: calc(var(--grid-item) * 13);
  }

  &--14 {
    width: calc(var(--grid-item) * 14);
  }

  &--15 {
    width: calc(var(--grid-item) * 15);
  }

  &--16 {
    width: calc(var(--grid-item) * 16);
  }

  &--17 {
    width: calc(var(--grid-item) * 17);
  }

  &--18 {
    width: calc(var(--grid-item) * 18);
  }
}
