.request__inp {
  border: none;
  border-bottom: 1px solid #cccccc;
  font-size: 17px 27px;
  padding: 3px 0;
  color: #000;
  outline: none;
  box-sizing: border-box;
  width: 100%;
  height: 34px;
  background-color: transparent;
  position: relative;
  z-index: 2;

  .request__dl--error & {
    color: #ee4e00;
    border-color: #ee4e00;

    &:focus {
      border-color: #ff5400;
    }
  }
}
